/* Base Styles */
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 13px 7.5% 10px 7.5%;
    background-color: white;
    color: black;
    margin-bottom: 50px;
    width: 85%;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    position: fixed;
    /* Default to fixed */
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: 2000;
}

#forflex {
    width: 75%;
    display: flex;
    justify-content: space-between;
}

.logo {
    font-size: 24px;
    margin-top: 10px;
    cursor: pointer;
}

.hamburger {
    display: none;
    position: relative;
    width: 30px;
    height: 30px;
    cursor: pointer;
    font-size: 25px;
}

.dropdown-tabs>a>img {
    width: 60px;
    margin-right: 15px;
}

.dropdown-tabs>a>div>h1 {
    font-size: 18px;
}

.dropdown-tabs>a>div>p {
    font-size: 15px;
    color: #6d6d6d;
}

.hamburger::before,
.hamburger::after,
.hamburger div {
    content: '';
    position: absolute;
    width: 150%;
    height: 20px;
    left: 0;
    transition: all 0.3s;
}

.hamburger::before {
    top: 0;
}

.hamburger div {
    top: 50%;
    transform: translateY(-50%);
}

.hamburger::after {
    bottom: 0;
}

.tabs {
    display: flex;
    align-items: center;
}

.tabs ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
    gap: 20px;
    /* Add gap between each tab */
}

.tabs ul li {
    margin-right: 20px;
}

.tabs ul li a {
    font-size: 20px;
    color: black;
    text-decoration: none;
    font-weight: bold;
    display: flex;
    align-items: center;
}

.dropdown-content a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
    font-weight: normal;
    background-color: #ffffff;
    /* box-shadow: rgba(0, 0, 0, 0.02) 0px 1px 3px 0px, rgba(27, 31, 35, 0.15) 0px 0px 0px 1px; */
}

.dropdown {
    position: relative;
    background-color: #ffffff;
}

.dropdown-content {
    top: 47px;
    position: absolute;
    background-color: #ffffff;
    min-width: 400px;
    display: none;
    color: black;
    z-index: 200000000;
}

.dropdown-content.show {
    display: block;
    background-color: #ffffff;
    z-index: 2;
    box-shadow: rgba(0, 0, 0, 0.02) 0px 1px 3px 0px, rgba(27, 31, 35, 0.15) 0px 0px 0px 1px;
    border-radius: 10px;
    /* box-shadow: inset rgba(0, 0, 0, 0.02) 0px 1px 3px 0px, rgba(27, 31, 35, 0.15) 0px 0px 0px 1px; */
}

.dropdown-content.show>hr {
    width: 90%;
    color: #aaa5a5;
    margin-top: -10px;
}

.contact button {
    background-color: #20303F;
    color: white;
    border: none;
    padding: 10px 20px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 0;
    cursor: pointer;
    border-radius: 5px;
}

/* Media Query for Mobile Screens */
@media (max-width: 1000px) {
    .navbar {
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        padding: 2% 3%;
        position: static;
        /* Change to static for mobile */
        width: 94%;
        /* Make it full width on mobile */
        transform: none;
        /* Remove the transform property */
        left: 0;
        /* Reset left position */
    }

    .hamburger {
        display: block;
        height: 20px;
    }

    .dropdown-tabs>a>div>h1 {
        font-size: 16px;
    }

    .dropdown-tabs>a>div>p {
        font-size: 15px;
        color: #6d6d6d;
        width: 70%;
    }

    .dropdown-tabs>a>div>p {
        font-size: 14px;
        color: #6d6d6d;
    }

    .dropdown-tabs>a>img {
        width: 40px;
        margin-right: 15px;
    }

    .tabs {
        position: fixed;
        top: 0;
        right: -100%;
        height: 100%;
        width: 50%;
        background-color: #f8f9fa;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
        overflow-y: auto;
        transition: right 0.3s ease-in-out;
        z-index: 1000;
        box-shadow: -2px 0 5px rgba(0, 0, 0, 0.5);
    }

    .tabs.show {
        right: 0;
    }

    .tabs ul {
        flex-direction: column;
        width: 100%;
    }

    .tabs ul li {
        width: 100%;
        margin: 0;
    }

    .tabs ul li a,
    .contact button {
        display: block;
        width: 100%;
        text-align: left;
        padding: 10px 15px;
        display: flex;
    }

    .dropdown-content {
        position: relative;
        top: 0px;
        /* left: -15px; */
    }

    .contact {
        width: 100%;
    }

    .contact button {
        background-color: #20303F;
        width: 80%;
        color: white;
        border: none;
        padding: 15px;
        margin: 20px;
        margin-right: 20px;
        text-align: center;
        text-decoration: none;
        display: inline-block;
        font-size: 16px;
        cursor: pointer;
        border-radius: 5px;
    }
}