
.DEmainDiagram{
    text-align: center;
    display: grid;
    row-gap: 150px;
    margin-top : 100px;
}

.DEdiagram-component{
    display: flex;
    justify-content: center;
}
.DECircleiteam{
    display: flex;
    justify-content: center;
    width: auto;
}

.DECircleiteam p{
    z-index: 3;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100px;
    border: 1px solid rgb(198, 196, 196);
    border-radius: 50px;
    box-shadow: 0px 0px 26.79px 0px rgba(0, 0, 0, 0.25);
    width: 300px;
    height: 50px;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
    background-color: white;
}
.DECircleiteam p:hover{
    border-bottom: 10px yellow;
}

.DEcircle_dig {
    background-color: #0096FE;
    margin: auto;
    padding: 2% 8% 7% 6%;
    height: 400px;
    width: 350px;
    border-radius: 60%;
    position: relative;
    top: -850px;
    box-shadow: 0px 3.25px 36.61px 0px rgba(0, 0, 0, 0.25);
    display: flex;
}   
 
.DEcircle_dig::before {
    content: '';
    position: absolute;
    top: -70px; 
    left: -70px; 
    right: -70px; 
    bottom: -50px; 
    border: 1px solid rgba(0, 150, 254); 
    border-radius: 50%;
    z-index: -1;
}

.DEcircle_dig::after {
    content: '';
    position: absolute;
    top: -90px;
    left: -70px;
    right: -70px;
    bottom: -80px;
    border-top: 7px solid var(--border-color); 
    border-radius: 50%;
    transition: border-color 0.3s ease;
}

.DEcircle_dig h1{
    width: 400px;
    font-size: 30px;
    font-weight: 700;
    line-height: 38.42px;
    text-align: center;
    color: white;
}

.DEcircle_dig li{
    width: 312px;
    font-size: 15px;
    font-weight: 500;
    line-height: 18px;
    text-align: left;
    margin-left: 20px;
    
}
.DEcircle_dig ul{
    display: flex;
    justify-content: right;
}

.DEcircle_dig div{
    margin-top: 10%;
    color: white;
}

.DEnavigation-buttons {
    width: 100%;
    display: flex;
    position: absolute;
    left: 40%;
    top: 75%;
    column-gap: 20px;
}
.DEnavigation-buttons p{
    font-size: 15px;
    font-weight: 700;
    text-align: center;
}

.DEnavigation-buttons button {
    color: white;
    background-color:#0096FE;
    border: 1px solid white;
    padding: 10px 20px;
    border-radius: 50%;
    cursor: pointer;
    z-index: 5;
}

.DEnavigation-buttons button:hover {
    background-color: #0096FE;
}


@media (max-width: 2050px) {
    .DEcircle_dig::before {
        bottom: -100px; 
    }
}

@media (max-width: 1460px) {
    .DEcircle_dig::before {
        bottom: -100px; 
    }
    .DEcircle_dig {
        background-color: #0096FE;
        height: 450px;;
    }
}


@media screen and (min-width: 1280px) {
    .DEApp-modern-Circle {
        display: block;
    }

    .DECarousel {
        display: none;
    }
}

@media screen and (min-width: 300px) and (max-width: 1280px) {
    .DEApp-modern-Circle {
        display: none;
    }

    .DECarousel {
        display: block;
    }
}

