.service-accordion-container {
    width: 100%;
    color: #20303F;
}

.accordion-summary {
    display: flex;
    align-items: center;
    gap: 1rem;
    color: #42526B;
    font-size: var(--font-size6);

    & p {
        margin: 0;
        font-weight: bold;
    }
}

.accordion-icon {
    width: 50px;
    height: 50px;
}

.accordion-details-container {
    display: flex;
    justify-content: center;
    gap: 3rem;
}

.accordian-image {
    width: 40%;
}

.accordion-details-right {
    width: 40%;
}

.accordion-details-heading {
    font-size: var(--font-size5);
    font-weight: 700;
    margin: 0;
}

.accordion-details-desc {
    font-size: var(--font-size8);
}

.accordion-detail-bold {
    font-weight: bold;
}

.accordion-details-btn {
    display: flex;
    justify-content: end;
}

@media screen and (min-width:300px) and (max-width:490px) {
    .accordion-summary {
        font-size: 1rem;
    }

    .accordion-icon {
        width: 40px;
        height: 40px;
    }

    .accordion-details-container {
        flex-direction: column;
        gap: 1rem;

        & img {
            width: 100%;
        }
    }

    .accordion-details-right {
        width: 100%;
    }

    .accordion-details-heading {
        font-size: 1rem;
        width: 100%;
    }

    .accordion-details-desc {
        font-size: 0.875rem;
    }
}

@media screen and (min-width:601px) and (max-width:840px) {
    .accordion-summary {
        font-size: 1.3rem;
    }

    .accordion-icon {
        width: 50px;
        height: 50px;
    }

    .accordion-details-container {
        flex-direction: column;
        gap: 1rem;

        & img {
            width: 80%;
        }
    }

    .accordion-details-right {
        width: 100%;
    }

    .accordion-details-heading {
        font-size: 1.3rem;
        width: 100%;
    }

    .accordion-details-desc {
        font-size: 1rem;
    }
}