.section-2-container {
    width: 100%;
    position: relative;
    margin-bottom: 15rem
}

.blockchain-bg {
    width: 100%;
    height: 80dvh;
}

.section-2 {
    position: absolute;
    top: 30%;
    left: 10%;
    background-color: #0B63E5;
    color: white;
    width: 700px;
    padding: 3% 1% 1% 2%;
    border-top-right-radius: 50px;
}

.section-2-line1 {
    font-size: 22px;
    font-weight: bold;
}

.section-2-line2 {
    font-size: 3rem;
    width: 80%;
    font-weight: lighter;
    margin: 0;
}

.section-2-line3 {
    font-size: 3rem;
    font-weight: bolder;
    margin: 0;
    padding: 0;
}

.section-2-line4 {
    font-size: 1.125rem;
    width: 80%;
    margin-bottom: 2rem;
}

.section-2-bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

@media screen and (min-width:300px) and (max-width:490px) {
    .section-2-container {
        background-image: url('../../assets/Homepage/tech-blockchain.png');
        padding: 5% 0;
        margin-bottom: var(--margin-bottom-mobile);
    }

    .section-2-container>img {
        display: none;
    }

    .section-2 {
        position: relative;
        width: 90%;
        top: 0;
        left: 0;
        margin: auto;

        & img:nth-child(1) {
            width: 30%;
        }
    }

    .section-2-line1 {
        font-size: 0.75rem;
    }

    .section-2-line2,
    .section-2-line3 {
        width: 80%;
        font-size: 1.75rem;
    }

    .section-2-line4 {
        font-size: 0.625rem;
    }
}


@media screen and (min-width:601px) and (max-width:840px) {
    .section-2 {
        width: 50%;
        top: 20%;
    }

    .section-2-line1 {
        font-size: 1rem;
    }

    .section-2-line2,
    .section-2-line3 {
        width: 90%;
        font-size: 2rem;
    }

    .section-2-line4 {
        font-size: 0.8rem;
    }

    .itlogo {
        width: 10%;
    }
}