
/* One */
.about-containerOne {
    display: flex;
    column-gap: 70px;
    margin: 0px 10%;

    margin-top: 250px;

    margin-bottom: var(--margin-bottom-web);
}

.about-container-one {
    width: 50%;
    padding-right: 50px;
}

.about-container-subone {
    font-size: 28px;
    font-weight: 800;
    line-height: 35.28px;
    margin-top: 0px;
    margin-bottom: 0px;
}

.about-container-subtwo {
    margin-top: 12px;
    margin-bottom: 0px;
    line-height: 83px;
    font-size: 70px;
    font-weight: 200;
}

.about-container-subthree {
    margin: 0px;
    font-size: 70px;
    font-weight: 800;
    line-height: 1;
}

.about-container-subfour {
    margin-top: 26px;
    margin-bottom: 45px;
    font-size: 30px;
    font-weight: 500;
    line-height: 42px;
    letter-spacing: 4%;
}

.about-container-subfive {
    display: flex;
    gap: 13px;
    line-height: 1;
}

.about-container-btnone,
.about-container-btntwo {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 60px;
    border-radius: 50px;
    font-size: 18px;
}

.about-container-btnone {
    padding: 25px, 52px, 26px, 52px;
    width: 196px;
    color: white;
    background-color: #20303F;
    border: none;

}

.about-container-btntwo {
    width: 216px;
    color: black;
    background-color: white;
    padding-left: 8px, 10px, 8px, 10px;
}

.about-container-two {
    width: 100%;
    height: 500px;
    width: 500px;
    display: flex;
}

.about-container-two img {
    padding-top: 40px;
    margin-bottom: -75px;
    width: 100;
}

/* media 1 */

@media (max-width: 1280px) {
    .about-containerOne {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        grid-template-rows: auto;
        padding: 0px;
        margin-top: 0px;
    }

    .about-container-one {
        width: 100%;
        padding-right: 50px;
    }

    .about-container-two {
        margin-left: 0px;
    }
}

@media (max-width: 500px) {
    .about-containerOne {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        overflow: hidden;
        margin-top: 0px;
    }

    .about-container-subone {
        margin-top: 0px;
        font-size: 14px;
    }

    .about-container-subtwo {
        width: 100vw;
        margin-top: 6px;
        margin-bottom: 0px;
        line-height: 1;
        font-size: 35px;
        font-weight: lighter;
    }

    .about-container-subthree {
        width: 80vw;
        margin-top: 5px;
        font-size: 35px;
    }

    .about-container-subfour {
        width: 80vw;
        margin-top: 13px;
        margin-bottom: 23px;
        font-size: 16px;
        font-weight: normal;
        line-height: 20px;
    }

    .about-container-two {
        width: 75%;
    }

    .about-container-two img {
        width: 100vw;
        height: 40vh;
    }

    .about-container-btnone,
    .about-container-btntwo {
        height: 45px;
        font-size: 10px;
        padding: 0%;
    }

    .about-container-btnone {
        width: 120px;
        color: white;
    }

    .about-container-btntwo {
        width: 130px;
        color: black;
    }
    .about-container-two {
        height: auto;
    }
}

/* two */

.about-containerTwo {
    width: 100%;
    margin-bottom: var(--margin-bottom-web);
    position: relative;
}

.HadnShakeimg {
    width: 100%;
    object-fit: cover;
    clip-path: polygon(10% 0%, 100% 0%, 100% 80%, 90% 100%, 0% 100%, 0% 20%);
}

.about-containerTwo-one {
    position: absolute;
    bottom: -50px;
    left: 28%;
    transform: translateX(-50%);
    background: #0B63E5;
    color: white;
    border-top-right-radius: 50px;
    padding: 1% 2% 1% 2%;
    width: 35%;
}

.logoT {
    width: 120px;
    height: 25px;
}

.about-containerTwo-line1 {
    margin-top: 20px;
    margin-bottom: 0px;
    font-size: 20px;
    font-weight: bold;
}

.about-containerTwo-line2 {
    margin-top: 0px;
    font-size: 40px;
    font-weight: lighter;
    margin-bottom: 0px;
}

.about-containerTwo-bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.getConnected-button {
    font-size: 15px;
    font-weight: bold;
    color: white;
    background-color: #20303F;
    padding: 20px 40px 20px 40px;
    border: none;
    border-radius: 50px;
}

.about-containerTwo-bottom>img {
    width: 20%;
}

/* Media for 2nd */
@media (max-width: 1280px) {

    .about-containerTwo {
        height: 300px;
    }

    .HadnShakeimg {
        clip-path: polygon(5% 0%, 100% 0%, 100% 0%, 100% 100%, 0% 100%, 0% 20%);
    }

    .about-containerTwo-one {
        top: 70px;
        left: 50%;
        border-radius: 0%;
        padding: 1% 1% 4% 2%;
        width: 75vw;
        height: 60%;
    }

    .logoT {
        width: 70px;
        height: 15px;
    }

    .about-containerTwo-line1 {
        margin-top: 12px;
        margin-bottom: 0px;
        font-size: 12px;
        font-weight: 700;
    }

    .about-containerTwo-line2 {
        font-size: 25px;
        line-height: 30px;
        font-weight: 200;
    }

    .about-containerTwo-line2 span {
        font-size: 25px;
        line-height: 30px;
        font-weight: 800;
    }

    .getConnected-button {
        padding: 10.42px 21.67px 10.83px 21.67px;
        font-size: 14px;
        font-weight: 800;
        line-height: 20.37px;
    }

    .about-containerTwo-bottom>img {
        width: 10%;
        margin-top: 4%;
        height: 70px;
    }
}

@media (max-width: 500px) {
    .about-containerTwo {
        margin-top: -100px;
        margin-bottom: 400px;
    }

    .about-containerTwo {
        height: 400px;
        overflow: hidden;
    }

    .HadnShakeimg {
        width: 100%;
        object-fit: cover;
        clip-path: polygon(10% 0%, 100% 0%, 100% 80%, 90% 100%, 0% 100%, 0% 20%);
    }

    .about-containerTwo-one {
        top: 70px;
        left: 50%;
        height: 250px;
        width: 280px;
        padding: 5% 5% 4% 2%;
    }

    .about-containerTwo-bottom>img {
        width: 20%;
        margin-top: 5%;
        height: 50px;
    }

}


