.section-9 {
    margin-bottom: 10rem;
}

.section-9-top-container {
    text-align: center;

    & p:nth-child(1) {
        font-size: var(--font-size3);
        margin: 0;
    }

    & p:nth-child(2) {
        font-size: var(--font-size4);
        margin: 0;
    }

    & span {
        font-weight: bolder;
    }
}

.section-9-bottom-container {
    width: 80%;
    display: flex;
    margin: auto;
    justify-content: space-around;
    gap: 2rem;
    margin-top: 5rem;
}

.section-9-bottom-head {
    font-size: 1.875rem;
    font-weight: 500;
}

.section-9-bottom-desc {
    font-size: 1rem;
}

.section-9-bottom-left {
    width: 500px;
    border-radius: 2.5rem;
}

.section-9-bottom-left {
    background-image: url('../../assets/Homepage/horizon-1.png');
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.section-9-bottom-left-content {
    width: 80%;
    height: max-content;
    padding-top: 1rem;
    padding-left: 2rem;
}

.section-9-bottom-right {
    width: 700px;
    height: 400px;
    border-radius: 2.5rem;
    cursor: pointer;
}

.section-9-bottom-right {
    background-image: url('../../assets/Homepage/horizon-2.png');
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.section-9-bottom-right-content {
    width: 60%;
    padding-top: 1rem;
    padding-left: 2rem;
}

.section-9-button {
    margin-left: 2rem;
}

@media screen and (min-width:300px) and (max-width:490px) {
    .section-9 {
        margin-bottom: var(--margin-bottom-mobile);
    }

    .section-9-top-container {
        & p:nth-child(1) {
            font-size: var(--font-size5);
        }

        & p:nth-child(2) {
            font-size: var(--font-size7);
        }
    }

    .section-9-bottom-container {
        flex-direction: column;
        width: 100%;
        align-items: center;
        gap: 1.5rem;
    }

    .section-9-bottom-head {
        font-size: 1.5rem;
    }

    .section-9-bottom-desc {
        font-size: 0.8rem;
    }

    .section-9-bottom-left {
        width: 90%;
        height: 350px;
        border-radius: 2rem;
    }

    .section-9-bottom-left-content {
        width: 90%;
        padding-top: 0.5rem;
        padding-left: 1rem;
    }

    .section-9-bottom-right {
        width: 95%;
        height: 350px;
        border-radius: 2rem;
    }

    .section-9-bottom-right-content {
        width: 90%;
        padding-top: 0.5rem;
        padding-left: 1rem;
    }

    .section-9-button {
        margin-left: 1rem;
    }
}


@media screen and (min-width:601px) and (max-width:840px) {
    .section-9-top-container {
        & p:nth-child(1) {
            font-size: var(--font-size6);
        }

        & p:nth-child(2) {
            font-size: 1rem;
        }
    }

    .section-9-bottom-container {
        flex-direction: column;
    }

    .section-9-bottom-head {
        font-size: 1.5rem;
    }

    .section-9-bottom-desc {
        font-size: 1rem;
    }

    .section-9-bottom-left,
    .section-9-bottom-right {
        width: 90%;
        margin: auto;
        height: 300px;
    }
}