.CMmainDiagram {
    text-align: center;
    display: grid;
    row-gap: 45px;
}

.CMdiagram-component {
    display: flex;
    justify-content: center;
}

.CMCircleiteam {
    display: flex;
    justify-content: center;
    width: auto;
}

.CMCircleiteam p {
    z-index: 3;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100px;
    border: 1px solid rgb(198, 196, 196);
    border-radius: 50px;
    box-shadow: 0px 0px 26.79px 0px rgba(0, 0, 0, 0.25);
    width: 300px;
    height: 50px;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
    background-color: white;
}

.CMCircleiteam p:hover {
    border-bottom: 10px yellow;
}

.CMcircle_dig {
    background-color: #0096FE;
    margin: auto;
    padding: 1% 8% 7% 6%;
    height: 450px;
    width: 380px;
    border-radius: 60%;
    position: relative;
    top: -800px;
    box-shadow: 0px 3.25px 36.61px 0px rgba(0, 0, 0, 0.25);
    display: flex;
}

.CMcircle_dig::before {
    content: '';
    position: absolute;
    top: -70px;
    left: -70px;
    right: -70px;
    bottom: -80px;
    border: 1px solid rgba(0, 150, 254);
    border-radius: 50%;
    z-index: -1;
}

.CMcircle_dig::after {
    content: '';
    position: absolute;
    top: -90px;
    left: -70px;
    right: -70px;
    bottom: -80px;
    border-top: 7px solid var(--border-color);
    border-radius: 50%;
    transition: border-color 0.3s ease;
}

.CMcircle_dig h1 {
    width: 400px;
    font-size: 30px;
    font-weight: 700;
    line-height: 38.42px;
    text-align: center;
    color: white;
}

.CMcircle_dig li {
    width: 312px;
    font-size: 15px;
    font-weight: 500;
    line-height: 18px;
    text-align: left;
    margin-left: 20px;

}

.CMcircle_dig div {
    margin-top: 10%;
    color: white;
}
.CMcircle_dig ul{
    display: flex;
    justify-content: right;
}

.CMnavigation-buttons {
    width: 100%;
    display: flex;
    position: absolute;
    left: 40%;
    top: 75%;
    column-gap: 20px;
}

.CMnavigation-buttons p {
    font-size: 15px;
    font-weight: 700;
    text-align: center;
}

.CMnavigation-buttons button {
    color: white;
    background-color: #0096FE;
    border: 1px solid white;
    padding: 10px 20px;
    border-radius: 50%;
    cursor: pointer;
    z-index: 5;
}

.CMnavigation-buttons button:hover {
    background-color: #0096FE;
}

@media screen and (min-width: 1281px) {
    .CMApp-modern-Circle {
        display: block;
    }

    .CMCarousel {
        display: none;
    }
}

@media screen and (min-width: 300px) and  (max-width: 1280px) {
    .CMApp-modern-Circle {
        display: none;
    }

    .CMCarousel {
        display: block;
    }
}

@media(max-width: 1750px){
    .CMcircle_dig::before {
        bottom: 0px; 
    }
    .CMcircle_dig {
        height: 500px;
        top: -800px;
    }
}

@media(max-width: 2000px){
    .CMcircle_dig::before {
        bottom: -100px;
    }
}
