.techstack-model {
    width: 70%;
    margin: auto;
    text-align: center;
    margin-bottom: 10rem;
}

.techstack-options {
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 1rem;

    & button {
        padding: 12px 40px;
        font-size: 1.1rem;
        font-weight: bold;
        cursor: pointer;
        border: 1px solid gray;
        border-radius: 20px;
        background-color: white;
    }
}

.techstack-heading {
    font-size: 3rem;
    margin: 0;
}

.techstack-desc {
    font-size: 1.3rem;

}

.techstack-heading-bold {
    font-weight: bold;
}

.techstack-container {
    display: grid;
    border-radius: 1rem;
    grid-template-columns: repeat(5, 1fr);
    box-shadow: rgba(17, 17, 26, 0.1) 0px 0px 16px;
    padding: 2rem;
    gap: 1rem;

    & div {
        margin: auto;
        padding: 32px 16px;
        font-size: 1rem;
        font-weight: 700;
        border-radius: 8px;
        text-align: center;
        width: 70%;
        height: max-content;
        box-shadow: rgba(17, 17, 26, 0.1) 0px 0px 16px;

        & img {
            width: 50%;
            height: auto;
            aspect-ratio: 1/1;
        }
    }
}


@media screen and (min-width:300px) and (max-width:490px) {
    .techstack-model {
        width: 90%;
        margin-bottom: var(--margin-bottom-mobile);
    }

    .techstack-heading {
        font-size: var(--font-size8);
    }

    .techstack-desc {
        font-size: 0.75rem;
    }

    .techstack-options {
        flex-direction: column;

        & button {
            width: 100%;
            text-align: left;
            padding: 15px 0 20px;
            border-radius: 0;
            border: none;
            border-bottom: 1px solid lightgray;
            padding-left: 5%;
        }
    }

    .techstack-container {
        grid-template-columns: repeat(2, 1fr);
        padding: 0.5rem;

        & div {
            width: 70%;
            height: 100px;

            & img {
                width: 50%;
                height: 50px;
            }
        }


    }
}

@media screen and (min-width:601px) and (max-width:1030px) {
    .techstack-model {
        width: 90%;
        margin-bottom: var(--margin-bottom-mobile);
    }

    .techstack-heading {
        font-size: var(--font-size8);
    }

    .techstack-desc {
        font-size: 0.85rem;
    }

    .techstack-options {
        flex-direction: column;

        & button {
            width: 100%;
            text-align: left;
            padding: 15px 0 20px;
            border-radius: 0;
            border: none;
            border-bottom: 1px solid lightgray;
            padding-left: 5%;
        }
    }

    .techstack-container {
        grid-template-columns: repeat(3, 1fr);
        padding: 1rem;

        & div {
            width: 70%;
            height: 150px;
        }

        & img {
            width: 20%;
            height: auto;
        }
    }
}