
.mainDiagram{
    text-align: center;
    display: grid;
    row-gap: 25px;
}

.diagram-component{
    display: flex;
    justify-content: center;
}
.Circleiteam{
    display: flex;
    justify-content: center;
    width: auto;
}

.Circleiteam p{
    z-index: 3;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100px;
    border: 1px solid rgb(198, 196, 196);
    border-radius: 50px;
    box-shadow: 0px 0px 26.79px 0px rgba(0, 0, 0, 0.25);
    width: 300px;
    height: 50px;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
    background-color: white;
}
.Circleiteam p:hover{
    border-bottom: 10px yellow;
}

.circle_dig {
    background-color: #0096FE;
    margin: auto;
    padding: 2% 8% 7% 6%;
    height: 440px;
    width: 380px;
    border-radius: 60%;
    position: relative;
    top: -750px;
    box-shadow: 0px 3.25px 36.61px 0px rgba(0, 0, 0, 0.25);
    display: flex;
}
 
.circle_dig::before {
    content: '';
    position: absolute;
    top: -70px; 
    left: -70px;
    right: -70px;
    bottom: -50px;
    border: 1px solid rgba(0, 150, 254); 
    border-radius: 50%;
    z-index: -1;
}

.circle_dig::after {
    content: '';
    position: absolute;
    top: -90px;
    left: -70px;
    right: -70px;
    bottom: -80px;
    border-top: 7px solid var(--border-color); 
    border-radius: 50%;
    transition: border-color 0.3s ease;
}

.circle_dig h1{
    width: 400px;
    font-size: 30px;
    font-weight: 700;
    line-height: 38.42px;
    text-align: center;
    color: white;
}

.circle_dig li{
    width: 312px;
    font-size: 15px;
    font-weight: 500;
    line-height: 18px;
    text-align: left;
    margin-left: 20px;
    
}

.circle_dig div{
    margin-top: 10%;
    color: white;
}
circle_dig ul{
    display: flex;
    justify-content: right;
}

.navigation-buttons {
    width: 100%;
    display: flex;
    position: absolute;
    left: 40%;
    top: 75%;
    column-gap: 20px;
}
.navigation-buttons p{
    font-size: 15px;
    font-weight: 700;
    text-align: center;
}

.navigation-buttons button {
    color: white;
    background-color:#0096FE;
    border: 1px solid white;
    padding: 10px 20px;
    border-radius: 50%;
    cursor: pointer;
    z-index: 5;
}

.navigation-buttons button:hover {
    background-color: #0096FE;
}


@media (max-width: 1900px){
    .circle_dig::before {
        bottom: -100px;
    }
}
@media screen and (min-width: 1280px) {
    .App-modern-Circle {
        display: block;
    }

    .Carousel {
        display: none;
    }
}

@media screen and (min-width: 300px) and (max-width: 1280px) {
    .App-modern-Circle {
        display: none;
    }

    .Carousel {
        display: block;
    }
}