.app-mod-section-1 {
    display: flex;
    color: #20303F;
    width: 80%;
    margin: auto;
    margin-bottom: var(--margin-bottom-web);
    margin-top: 250px;
}

.app-mod-section-1-left p:nth-child(1) {
    margin: 0;
    font-size: var(--font-size1);
    font-weight: bold;
}

.app-mod-section-1-left p:nth-child(2) {
    font-size: var(--font-size2);
    font-weight: lighter;
    margin: 0;
}

.app-mod-section-1-left p:nth-child(3) {
    margin: 0;
    font-size: var(--font-size2);
    font-weight: bolder;
    margin: 0;
}

.app-mod-section-1-right {
    & img {
        width: 100%;
    }
}

.app-mod-section-3 {
    display: flex;
    margin: auto;
    width: 80%;
    justify-content: center;
}

.app-mod-section-3-left {
    width: 40%;
}

.app-mod-section-3-right {
    width: 50%;

    & img {
        width: 100%;
    }
}

.app-mod-section-3-left p:nth-child(1) {
    font-size: var(--font-size3);
    margin: 0;
    font-weight: lighter;
}

.app-mod-section-3-left p:nth-child(2) {
    font-size: var(--font-size3);
    margin: 0;
    color: #20303F;
    font-weight: bold;
}

.app-mod-section-3-left p:nth-child(3) {
    font-size: var(--font-size3);
    margin: 0;
    color: #0B63E5;
    font-weight: bold;
}

.app-mod-section-3-left p:nth-child(4) {
    font-size: var(--font-size4);
    width: 70%;
}

.app-mod-section-4 {
    width: 100%;
    padding-top: 10rem;
    background-image: url('../../assets/Services/AppModernization/ellipse.png');
    background-repeat: no-repeat;
    background-position: center;
    margin-bottom: var(--margin-bottom-web);
}

.app-mod-section-4>div {
    width: 80%;
    margin: auto;
}

.app-mod-section-4-top {
    text-align: center;
    margin-bottom: 5rem;

    & p {
        margin: 0;
    }
}

.app-mod-section-4-top p:nth-child(1),
.app-mod-section-4-top p:nth-child(2) {
    font-size: var(--font-size5);
}

.app-mod-section-4-top p:nth-child(2) {
    font-weight: bold;
}

.app-mod-section-4-top p:nth-child(3) {
    font-size: var(--font-size4);
}

.app-mod-section-4-bottom {
    display: flex;
    justify-content: space-between;

    & div {
        width: 28%;
        border-radius: 1rem;
        box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
        padding: 3rem 1rem 1.5rem;
        background-color: white;
    }
}

.app-mod-section-4-bottom div p:nth-child(2) {
    font-size: var(--font-size6);
    font-weight: bold;
    color: #292962
}

.app-mod-section-4-bottom div p:nth-child(3) {
    font-size: var(--font-size7);
    width: 80%;
    color: #6B7989;
}

