}

.app-mod-section-5-One>:nth-child(2) {
    margin-top: 0px;
    font-size: 36px;
    font-weight: 800;
    line-height: 48px;
    text-align: center;
}

.app-mod-section-5-One>:nth-child(3) {
    margin-top: 12px;
    font-size: 20px;
