.app-mod-section-6 {
    width: 80%;
    margin: auto;
    margin-top: -500px;
    margin-bottom: var(--margin-bottom-web);
}

.app-mod-section-6-top {
    width: 55%;
    font-size: var(--font-size5);
    margin-bottom: 5rem;

    & p {
        margin: 0;
    }
}

.app-mod-section-6-top p:nth-child(1) {
    font-weight: lighter;
}

.app-mod-section-6-top p:nth-child(2) {
    font-weight: bold;
}

.app-mod-section-6-bottom {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 4rem;

    & div {
        box-shadow: rgba(17, 17, 26, 0.1) 0px 0px 16px;
        padding: 3rem 1rem 1.5rem;
        border-radius: 1rem;
    }
}

.app-mod-section-6-bottom>div p:nth-child(2) {
    font-size: var(--font-size6);
    font-weight: bold;
    color: #292962
}

.app-mod-section-6-bottom>div p:nth-child(3) {
    font-size: var(--font-size7);
    color: #6B7989;
}

.app-mod-section-7 {
    width: 100%;
    margin: auto;
    margin-bottom: var(--margin-bottom-web);
    background-image: url('../../assets/Services/AppModernization/section-7.png');
    background-position: center;
    background-size: cover;
}

.app-mod-section-7>div {
    width: 30%;
    font-size: var(--font-size6);
    margin-left: 10%;
    padding: 1%;

    & p:nth-child(3) {
        font-weight: bold;
    }
}

.app-mod-section-8 {
    width: 80%;
    margin: auto;
    margin-bottom: var(--margin-bottom-web);
}

.app-mod-section-8-top {
    font-size: var(--font-size5);
    margin-bottom: 3rem;

    & p {
        margin: 0;
    }

    & p:nth-child(2) {
        font-weight: bold;
    }
}

.app-mod-section-8-bottom {
    display: flex;
    justify-content: space-between;

    & div {
        width: 28%;
        background-color: #0B63E5;
        color: white;
        padding: 1%;
        border-radius: 1rem;

        & img {
            width: 100%;
            aspect-ratio: 16/9;
            border-radius: 1rem;
        }

        & p {
            font-size: 28px;
        }

        & ul {
            font-size: 18px;
            font-weight: lighter;
            width: 80%;
        }
    }
}

.app-mod-section-9 {
    font-size: var(--font-size3);
    width: 90%;
    margin: auto;
    margin-bottom: var(--margin-bottom-web);
    text-align: center;
    font-weight: lighter;

    @media screen and (max-width:1280px) {
        font-size: 2.25rem;
    }
}


@media screen and (min-width:300px) and (max-width:490px) {
    .app-mod-section-1 {
        flex-direction: column;
        margin-top: 0px;
        margin-bottom: var(--margin-bottom-mobile);
    }

    .app-mod-section-1-left p:nth-child(1) {
        font-size: 21px;
    }

    .app-mod-section-1-left p:nth-child(2) {
        font-size: 41px;
    }

    .app-mod-section-1-left p:nth-child(3) {
        font-size: 41px;
    }

    .app-mod-section-3 {
        flex-direction: column;
        width: 90%;
        margin-bottom: var(--margin-bottom-mobile);
    }

    .app-mod-section-3-left,
    .app-mod-section-3-right {
        width: 100%;
    }

    .app-mod-section-3-left p:nth-child(1),
    .app-mod-section-3-left p:nth-child(2),
    .app-mod-section-3-left p:nth-child(3) {
        font-size: 41px;
    }

    .app-mod-section-3-left p:nth-child(4) {
        font-size: 1rem;
        width: 80%;
    }

    .app-mod-section-4 {
        margin-bottom: var(--margin-bottom-mobile);
        padding: 1rem 0;
    }

    .app-mod-section-4>div {
        width: 90%;
    }

    .app-mod-section-4-bottom {
        flex-direction: column;
        gap: 1rem;

        & div {
            width: 85%;
            margin: auto;
        }
    }

    .app-mod-section-4-bottom div p:nth-child(2) {
        font-size: var(--font-size4);
    }

    .app-mod-section-6 {
        width: 90%;
        margin-top: 0;
        margin-bottom: var(--margin-bottom-mobile);
    }

    .app-mod-section-6-top {
        width: 100%;
        font-size: 2rem;
    }

    .app-mod-section-6-bottom {
        grid-template-columns: repeat(1, 1fr);
        gap: 1rem;
    }

    .app-mod-section-6-bottom>div p:nth-child(2) {
        font-size: var(--font-size4);
    }

    .app-mod-section-7 {
        width: 90%;
        margin-bottom: var(--margin-bottom-mobile);
    }


    .app-mod-section-7>div {
        width: 90%;
        font-size: 18px;
    }

    .app-mod-section-8 {
        width: 90%;
        margin-bottom: var(--margin-bottom-mobile);
    }

    .app-mod-section-8-bottom {
        flex-direction: column;
        gap: 1rem;

        & div {
            width: 90%;
            margin: auto;
            padding: 2%;
        }
    }

    .app-mod-section-9 {
        font-size: 1.2rem;
        width: 90%;
        margin-bottom: var(--margin-bottom-mobile);
    }

    section {
        margin-bottom: var(--margin-bottom-mobile);
    }
}

@media screen and (min-width:601px) and (max-width:840px) {

    .app-mod-section-1-left p:nth-child(2),
    .app-mod-section-1-left p:nth-child(3) {
        font-size: 45px;
    }

    .app-mod-section-1-right {
        & img {
            width: 100%;
        }
    }

    .app-mod-section-3 {
        flex-direction: column;
        width: 100%;
    }

    .app-mod-section-3-left {
        width: 80%;
        margin: auto;
    }

    .app-mod-section-3-right {
        width: 90%;
        margin: auto;

        & img {
            width: 100%;
        }
    }

    .app-mod-section-3-left p:nth-child(1),
    .app-mod-section-3-left p:nth-child(2),
    .app-mod-section-3-left p:nth-child(3) {
        font-size: 45px;
        text-align: center;
    }

    .app-mod-section-3-left p:nth-child(4) {
        width: 90%;
        margin: auto;
        font-size: 30px;
        margin-bottom: 2rem;
    }

    .app-mod-section-4-bottom {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        gap: 1rem;

        & div {
            width: 80%;
            margin: auto;
        }
    }

    .app-mod-section-6 {
        width: 90%;
        margin-top: 0;
    }

    .app-mod-section-6-top {
        width: 90%;
    }

    .app-mod-section-6-bottom {
        grid-template-columns: repeat(2, 1fr);
    }

    .app-mod-section-7>div {
        width: 70%;
    }

    .app-mod-section-8 {
        width: 90%;
    }

    .app-mod-section-8-bottom {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        gap: 2rem;

        & div {
            width: 80%;
            margin: auto;

            & img {
                width: 100%;
            }
        }
    }
}

@media screen and (min-width:841px) and (max-width:1450px) {

    .app-mod-section-1-left p:nth-child(2),
    .app-mod-section-1-left p:nth-child(3) {
