    }

    .app-mod-section-1-right {
        & img {
            width: 80%;
        }
    }

    .app-mod-section-3 {
        width: 80%;
    }

    .app-mod-section-3-left {
        width: 70%;
    }

    .app-mod-section-3-right {
        width: 45%;
        display: flex;
        align-items: center;

        & img {
            width: 100%;
        }
    }

    .app-mod-section-4 {
        margin-bottom: var(--margin-bottom-mobile);
        padding: 1rem 0;
    }

    .app-mod-section-8-bottom {

        & p {
            font-size: 22px;
        }

        & ul {
            font-size: 10px;
        }
    }

    .app-mod-section-9 {
        width: 90%;
        font-size: 2rem;
    }
}