.section-1 {
    width: 85%;
    margin: auto;
    display: flex;
    justify-content: space-between;
    margin-bottom: var(--margin-bottom-mobile);
    margin-top: 250px;
}

.section-1-heading {
    font-size: 5rem;
    color: '#20303F';

    & p {
        margin: 0;
    }
}

.section-1-buttons {
    display: flex;
    margin-bottom: 2.5rem;
    gap: 1rem
}

.section-1-bottom {
    font-size: var(--font-size1);
    border-top: 1px solid black;
    padding-top: 1rem;
}

.bold-heading {
    font-weight: bolder;
}

.section-1-desc {
    font-size: 2.2rem;
}

.partners {
    display: flex;
    gap: 1rem;
}

@media screen and (min-width:300px) and (max-width:490px) {
    .section-1 {
        flex-direction: column;
        gap: 2rem;
        width: 95%;
        margin-top: 0px;
        margin-bottom: 3rem;
    }

    .section-1-heading {
        font-size: 2.625rem;
    }

    .section-1-desc {
        font-size: 1.125rem;
        width: 60%;
    }

    .section-1-bottom {
        font-size: 1rem;
    }

    .section-1-right img {
        width: 93%;
    }
}

@media screen and (min-width:601px) and (max-width:840px) {
    .section-1 {
        width: 90%;
        justify-content: space-between;
        margin-top: 0px;
    }

    .section-1-heading {
        font-size: 2rem;
    }

    .section-1-desc {
        font-size: 1rem;
    }

    .section-1-bottom {
        font-size: 0.8rem;
    }

    .section-1-right img {
        width: 100%;
    }
}