.services-section-2 {
    position: relative;
    width: 100%;
    margin-bottom: 20rem;
}

.section-2-bg {
    width: 100%;
}

.services-section-2-content {
    position: absolute;
    top: 60%;
    left: 10%;
    background-color: #0B63E5;
    color: white;
    width: 700px;
    padding: 3% 1% 1% 2%;
    border-top-right-radius: 50px;
}

.services-section-head {
    font-size: var(--font-size3);
    margin: 0;
    font-weight: lighter;
}

.bold-head {
    font-weight: 1000;
}

.services-section-desc {
    font-size: var(--font-size4);
}

.services-section-2-content-bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

@media screen and (min-width:300px) and (max-width:490px) {
    .services-section-2 {
        margin-bottom: 5rem;
        overflow: hidden;
    }

    .section-2-bg {
        width: 100vh;
    }

    .section-2-logo {
        width: 10%;
    }

    .services-section-2-content {
        position: absolute;
        top: 50%;
        left: 50%;
        width: 90%;
        transform: translate(-50%, -50%);
    }

    .services-section-head {
        font-size: 1.5rem;
    }

    .services-section-desc {
        font-size: 0.65rem;
    }

    .itlogo {
        width: 10%;
    }
}

@media screen and (min-width:600px) and (max-width:1024px) {
    .services-section-2 {
        margin-bottom: 20rem;
    }

    .section-2-logo {
        width: 15%;
    }

    .services-section-2-content {
        width: 70%;
    }

    .services-section-head {
        font-size: 1.7rem;
    }

    .services-section-desc {
        font-size: 0.9rem;
    }
}