.subcareerContainer{
    display: flex;
    column-gap: 10px;
}
.subcareerContainer-one{
    width: 33%;
}

.careerItem {
    display: flex;
    height: max-content;
    justify-content: space-between;
    align-items: center;
    padding: 5%;
    border: 1px solid #ccc;
    border-radius: 10px;
}
.careerItem * {
   box-sizing: border-box;
}

.careerContent>:nth-child(1){
    margin: 0px;
    font-size: 20px;
    font-weight: 500;
    line-height: 24.2px;
    color: rgba(39, 36, 34, 1);
}

/* Main Div2 */
.careerContent>:nth-child(2){
    margin-top: 4%;
    font-size: 16px;
    font-weight: 400;
    line-height: 19.36px;
    color: #7A7D9C;
}

.careerContent>:nth-child(2) span{
    color: #0B63E5;
    font-size: 16px;
    font-weight: 400;
    line-height: 19.36px;
}

.careerContent>:nth-child(3){
    height: 44px;
    font-size: 15px;
    font-weight: 400;
    line-height: 21px;
    margin-bottom: 0px;
}

.careerArrow {
    display: flex;
    align-items: center;
    padding: 1px;
    border: 1px 0px 0px 0px;
    width: 115px;
    height: 60px;
}
.careerArrow img{
    width: 100%;
}

.subcareerContainer-two{
    width: 60%;
    padding: 1%;
}

.JobDescription{
    height: 25px;
    font-family: Plus Jakarta Sans;
    font-size: 27px;
    font-weight: 600;
    line-height: 25px;
}
.JobDescription span{
    color: rgba(46, 166, 143, 1);
}
.JobLocation{
    width: 257px;
    font-family: Plus Jakarta Sans;
    font-size: 20px;
    font-weight: 700;
    line-height: 25.79px;
}

.Jobresponsibility{
    font-family: Plus Jakarta Sans;
    font-size: 20px;
    font-weight: 700;
    line-height: 25.79px;
    margin-bottom: 25px;
}
.Jobresponsibility p{
    margin-bottom: 25px;
}
.Jobresponsibility ul {
    font-family: Plus Jakarta Sans;
    font-size: 15.12px;
    font-weight: 400;
    line-height: 30.95px;
    margin: 0px;
}

.JobSkill{
    font-family: Plus Jakarta Sans;
    font-size: 20px;
    font-weight: 700;
    line-height: 25.79px;
}
 
.JobSkill p{
    margin-bottom: 25px;
}

.JobSkill ul{
    font-family: Plus Jakarta Sans;
    font-size: 15.12px;
    font-weight: 400;
    line-height: 30.95px;
    margin: 0px;
}

.Linetag{
    color: #0B63E5;
    border: 1px solid;
    margin-top: -15px;
}
