.section-3-container {
    width: 100%;
    margin: auto;
    margin-bottom: var(--margin-bottom-mobile);
    background-image: url('../../assets/Homepage/wavelength.png');
    background-size: cover;
    background-repeat: no-repeat;
}

.section-3-container>div p {
    margin: 0;
}

.section-3-top-line1 {
    width: 70%;
    margin: 0 auto;
    font-size: 3rem;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 0.2rem;
    font-weight: lighter;
}

.section-3-top-line2 {
    margin: 0;
    font-size: 3rem;
    text-align: center;
    font-weight: bolder;
}

.section-3-bottom {
    display: flex;
    width: 70%;
    margin: auto;
    font-size: var(--font-size1);
    justify-content: space-evenly;
    font-weight: 600;

    & div {
        width: 18%;

    }

    & hr {
        color: #0B63E5;
        border: 2px solid #0B63E5;
        margin: 2rem 0;
    }
}

.section-3-end {
    width: 70%;
    font-size: 20px;
    font-style: italic;
    text-align: center;
    margin: 2.5rem auto;
}

@media screen and (min-width:300px) and (max-width:490px) {
    .section-3-container {
        margin-bottom: var(--margin-bottom-mobile);
        background-image: none;
    }

    .section-3-top-line1 {
        width: 100%;
        font-size: 1rem;
        gap: 0.1rem;

        & img {
            width: 10%;
        }
    }

    .section-3-top-line2 {
        font-size: 1rem;
    }

    .section-3-bottom {
        display: grid;
        gap: 1rem;
        grid-template-columns: repeat(2, 1fr);
        margin-top: 3rem;
    }

    .section-3-bottom div {
        width: 90%;
        font-size: 0.75rem;

        & img {
            width: 30%;
        }

        & hr {
            margin: 1rem 0;
        }
    }

    .section-3-end {
        font-size: 14px;
    }
}

@media screen and (min-width:601px) and (max-width:840px) {
    .section-3-container {
        margin-bottom: 3rem;
        background-image: none;
    }

    .section-3-top-line1 {
        font-size: 1.5rem;
        gap: 0.2rem;

        & img {
            width: 15%;
        }
    }

    .section-3-top-line2 {
        font-size: 1.5rem;
    }

    .section-3-bottom {
        display: grid;
        gap: 1rem;
        grid-template-columns: repeat(2, 1fr);
        margin-top: 3rem;
    }

    .section-3-bottom div {
        width: 90%;
        font-size: 1rem;

        & img {
            width: 30%;
        }

        & hr {
            margin: 1rem 0;
        }
    }
}