#main-cont{
    background-image: url("../assets/footerbg.jpg");
    width: 100%;
    background-size: cover;
    background-position: center;
    color: #ffffff;
}

#foot-sec1{
    display: flex;
    justify-content: center;
    padding: 0% 17.5%;
    margin-bottom: 100px;
}
#foot-sec1>:nth-child(1){
    border-bottom-left-radius: 20px;
}
#foot-sec1>:nth-child(2){
    border-radius: 20px 20px 20px 0px;
}

#sec1-prt1{
    background-color: #0034B0;
    padding: 40px 5%;
    width: 472px;
}

#sec1-prt1>div>h1{
    font-size: 40.5px;
    font-weight: 600;
    width: 80%;
}

#sec1-prt1>div>p{
    font-family: Plus Jakarta Sans;
    font-size: 20.27px;
    font-weight: 500;
    line-height: 29.49px;
}
#sec1-prt1>div>div>img{
    width: 44px;
    height: 44px;
    margin-right: 20px;
}

#sec1-prt1>div>div{
    display: flex;
    justify-content: left;
    align-items: center;
}

#sec1-prt2{
    background-color: #ffffff ;
    width: 668px;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    z-index: 2;
    margin-top: -60px;
    position: sticky;
    bottom: -100px;
    color: #000000;
    text-align: left;
    padding: 20px;
    padding-right: 80px;
}

.input-tag-footer{
    height: 50px !important;
    width: 100% !important;
    border-radius: 15px !important;
    border: 1px solid rgb(199, 195, 195) !important;
    padding-left: 30px !important;
}

#phone{
    display: flex;
}

#phone>#phone-no{
    width: 70%;
}

#country-selector{
    padding: 0px 10px;
    width: 30%;
    margin-right: 10px;
    border-radius: 15px;
    border: 1px solid rgb(199, 195, 195);

}

.inputStyle,.containerStyle{
    width: 1000px !important;
}

input[type='number']::-webkit-inner-spin-button,
input[type='number']::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

input,textarea{
    font-size: 20px;
}

#sec1-prt2>form>textarea{
    height: 70px;
    width: 100%;
    padding-left: 30px;
    padding-top: 10px;
    border-radius: 15px;
    border: 1px solid rgb(199, 195, 195);
}

#sec1-prt2 :placeholder-shown{
    font-size: 18px;
    font-family: 'Plus Jakarta Sans', sans-serif;
}

#submit-footer-form{
    margin-top: 50px;
    margin-left: 72%;
    width: 200px;
    height: 60px;
    border-radius: 50px;
    border: none;
    background-color: #201D1D;
    color: #ffffff;
}

.foot-tabs>p{
    cursor: pointer;
}

@media (max-width: 1450px){
    #submit-footer-form{
        margin-left: 62%;
    }
}


#foot-sec2{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin: 0 10% 50px 10%;
}

#foot-sec2>div>img{
    margin-top: 50px;
    width: 150px;
}


#foot-sec2>div>h2{
    width: 100%;
    /* border: 1px solid red; */
}

#foot-sec2>div>hr{
    height: 300px;
    /* margin-right: 200px; */
}

#foot-sec2>:nth-child(1){
    width: 20%;
}
.foot-tabs>div{
    display: flex;
    gap: 15px;
}
.foot-tabs>div>a>img{
    width: 30px;
}
.foot-tabs>div>:nth-child(3)>img{
    width: 15px;
}

.foot-tabs>div>:nth-child(2)>img{
    width: 35px;
}

.foot-tabs>h3{
    margin-bottom: 30px;
}

#foot-sec3{
    display: flex;
    justify-content: space-between;
    width: 80%;
    text-align: center;
    margin-left: 10%;
}


@media (max-width: 1281px){

    #main-cont{
        background-image: url("../assets/footerbg.jpg");
        width: 100%;
        background-size: cover;
        background-position: center;
        margin-top: 0px;
    }

    #foot-sec1{
        display: flex;
        justify-content: center;
        padding: 0%;
        margin-bottom: -100px;
        border-radius: none;
    }

    #sec1-prt1{
        background-color: #0034B0;
        padding: 40px 5%;
        width: 100%;
    }

    #sec1-prt2{
        background-color: #ffffff ;
        width: 90%;
        box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
        z-index: 2;
        margin-top:0px;
        position:static;
        bottom: -100px;
        color: #000000;
        text-align: left;
        padding: 20px;
        padding-right: 80px;
        border-radius: none;
        padding-left: 45px;
    }
    
    #foot-sec1>:nth-child(2) {
        border-radius: 0px 0px 20px 0px;
        width: auto;
    }

    #submit-footer-form{
        margin-left: 52%;
    }

    #istab-view{
        display: grid;
        gap: 20px;
        /* text-align: center; */
        grid-template-columns: repeat(2,1fr);
    }
    #foot-sec2{
        display: block;
        justify-content: space-between;
        align-items: flex-start;
        margin: 0 10% 50px 10%;
    }

    #foot-sec2>div>img{
        margin: 140px 0px 30px 45%;
        width: 150px;
        transform: translateX(-50%);
    }

    
    #foot-sec2>div>hr{
        height: 0px;
    }

    #foot-sec2>div>h2{
        display: none;
    }

    #foot-sec2>:nth-child(1){
        width: 100%;
    }
}


@media (max-width: 500px){
    #foot-sec1{
        display: block;
        justify-content: center;
        padding: 0%;
        margin-bottom: -100px;
        border-radius: none;
    }

    #sec1-prt1{
        background-color: #0034B0;
        padding: 40px 5%;
        width: 90%;
    }
    #foot-sec1>:nth-child(2){
        border-radius: 0px;
    }
    #sec1-prt2{
        background-color: #ffffff ;
        width: 80%;
        box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
        z-index: 2;
        margin-top:0px;
        position:static;
        bottom: -100px;
        color: #000000;
        text-align: left;
        padding: 20px;
        padding-right: 80px;
        border-radius: none;
        padding-left: 20px;
    }

    #foot-sec1>:nth-child(1) {
        border-bottom-left-radius: 00px;
    }
}
