#blog-container{
    width: 100%;
    height: auto;
    padding: 50px 0px 100px 0px;
    /* border: 2px solid red; */
    background-image: url("../assets/blog/blogbg.png");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column;
}

#blog-container>#blog-main-cont{
    /* margin-bottom: 300px; */
    margin: auto;

}

#blog-container>h1{
    margin-left: 10%;
}


#blog-container h1{
    font-size: 40px;
    color: #000000;
    font-weight: 600;
    text-align: left;
}

#blog-container span{
    font-size: 40px;
    color: #007bff;
    font-weight: 600;
}

#blog-main-cont{
    width: 80%;
    height: 592px;
    margin: 0px 0px;
    display: flex;
    align-items: center;
    background-color: #007bff;
    border-radius: 50px 50px 50px 50px;
}

#blog-main-cont img{
    width: 50%;
    height: 592px;
    border-radius: 50px 0 0 50px;
}

#blog-main-cont>div{
    width: 30%;
    height: auto;
    margin: 200px;
}

#blog-main-cont>div>button{
    width: auto;
    padding: 18px 33px;
    border: none; 
    outline: none;
    border-radius: 50px; 
    background-color: #ffffff;
    color: #007bff;
    font-weight: bold;
    cursor: pointer;
    margin-bottom: 20px;
} 

#blog-main-cont h3{
    font-size: 40px;
    color: white;
    font-weight: 600;
    margin-bottom: 20px;
}

#blog-main-cont p{
    font-size: 18px;
    color: white;
    font-weight: 400;
    line-height: 1.5;
}

#blog-button-cont{
    width: 100%;
    height: auto;
    margin-top: 50px;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap: 0px;
}

.blogButtons{
    width: auto;
    min-width: max-content;
    height: auto;
    padding: 8px;
    border-radius: 50px;
    background-color: #007bff;
    color: white;
    font-weight: bold;
    cursor: pointer;
    border: 1px solid #ffffff;
    margin-bottom: 5px;
    margin-right: 5px;
}
@media (max-width: 1450px){
    #blog-main-cont>div{
        width: 40%;
        height: auto;
        margin: 100px;
    }
}
@media (max-width: 1680px){
    #blog-button-cont{
        grid-template-columns: repeat(3,1fr);
        gap: 0px;
    }
    #blog-main-cont>div{
        width: 40%;
        height: auto;
        margin: 100px;
    }
    
   }

/* Media Queries */

@media (max-width:1450px){
    #blog-button-cont{
        width: auto;
        min-width: max-content;
        height: auto;
        margin-top: 50px;
        display: grid;
        grid-template-columns: repeat(2,1fr);
        gap: 0px;
    }
}
@media (max-width: 1281px){
    #blog-main-cont {
        width: 90%;
        height: auto;
        flex-direction: row;
        border-radius: 50px 50px 50px 50px;
    }
    #blog-main-cont img {
        width: 50%;
        height: auto;
        border-radius: 50px 0 0 50px;
    }
    #blog-main-cont > div {
        width: 40%;
        /* margin-left: 5%; */
        /* margin: 0px; */
        margin: 0 0 0 5%;
    }
    #blog-main-cont h3 {
        font-size: 30px;
        margin-top: -10px;
    }
    #blog-main-cont p {
        font-size: 16px;
        margin-top: -10px;
    }
    #blog-button-cont{
        grid-template-columns: repeat(2,1fr);
        gap: 0px;
    }
    #blog-main-cont>div{
        padding:10px 0px;
    }
    #blog-main-cont>div>div{
        margin-top: -10px;
    }
}

@media (max-width: 500px){
    #blog-container h1,
    #blog-container span {
        font-size: 24px;
        text-align: center;
    }
    #blog-main-cont {
        width: 100%;
        height: auto;
        flex-direction: column;
        border-radius: 20px 20px 20px 20px;
    }
    #blog-main-cont img {
        width: 100%;
        height: auto;
        border-radius: 20px 20px 0 0;
    }
    #blog-main-cont > div {
        width: 95%;
        margin: 10px auto;
        margin: 0px;
    }
    #blog-main-cont h3 {
        font-size: 24px;
    }
    #blog-main-cont p {
        font-size: 14px;
    }
    #blog-main-cont > div > button {
        padding: 10px 20px;
        font-size: 14px;
    }
    #blog-button-cont {
        grid-template-columns: 1fr;
    }
    .blogButtons {
        margin-bottom: 10px;
        margin-right: 0;
        width: 100%;
    }
}
