.Career-container {
    margin-bottom: var(--margin-bottom-web);
}

.Career-containerOne {
    margin: 0% 10%;
    display: flex;
    justify-content: space-around;
    margin-top: 250px;
    margin-bottom: var(--margin-bottom-web);
}

.Career-container-oneTitle {
    margin-top: 0px;
    font-size: 22px;
    font-weight: 900;
    margin-bottom: 12px;
}

.Career-container-oneTitle2 {
    font-size: 82px;
    line-height: 83px;
    font-weight: lighter;
    margin-top: 0px;
    margin-bottom: 26px;
}

.Career-container-oneSubTitle {
    font-weight: bolder;
    line-height: 83px;
}

.Career-container-oneInfo {
    margin-top: 0px;
    font-size: 28px;
    line-height: 42px;
    margin-bottom: 40px;
    font-weight: 500;
    padding-right: 20px;
}

.career-container-subOne {
    display: flex;
    gap: 13px;
}

.career-container-btnone,
.career-container-btntwo {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 60px;
    border-radius: 50px;
    font-size: 18px;
}

.career-container-btnone {
    width: 204px;
    height: 60px;
    color: white;
    background-color: #20303F;
    border: none;
    padding: 27px 52px;
}

.career-container-btntwo {
    width: 200px;
    color: black;
    background-color: white;
}

.career-container-two {
    width: 50%;
}

.career-container-two img {
    padding-top: 40px;
    margin-bottom: 0px;
    width: 100;
}

/* mediaOne */
@media (max-width: 1280px) {
    .Career-containerOne {
        margin: 0% 10%;
        display: flex;
        flex-direction: column;
    }

    .career-container-two {
        width: 100%;
        display: flex;
        justify-content: center;
    }

    .career-container-two img {
        width: 60%;
    }
}

@media (max-width: 500px) {
    .Career-containerOne {
        margin: 0% 5%;
        display: flex;
        flex-direction: column;
    }

    .Career-container-oneTitle {
        margin-top: 0px;
        font-size: 20px;
    }

    .Career-container-oneTitle2 {
        width: 100vw;
        margin-top: 6px;
        margin-bottom: 0px;
        line-height: 1;
        font-size: 35px;
        font-weight: lighter;
    }

    .Career-container-oneInfo {
        width: 80vw;
        margin-top: 13px;
        margin-bottom: 23px;
        font-size: 16px;
        font-weight: normal;
        line-height: 20px;
    }

    .career-container-btnone,
    .career-container-btntwo {
        height: 40px;
        font-size: 10px;
        padding: 0%;
    }

    .career-container-btnone {
        width: 120px;
        color: white;

    }

    .career-container-btntwo {
        width: 130px;
        color: black;
    }

    .career-container-two {
        width: 75%;
        display: flex;
        justify-content: left;
        margin-bottom: 50px;
    }

    .career-container-two img {
        width: 80vw;
    }
}

