.section-4-container {
    width: 70%;
    margin: auto;
    margin-bottom: 10rem;
}

.section-4-container>div p {
    margin: 1rem 0;
}

.section-4-container>div>p {
    font-size: var(--font-size4);
}

.section-4-heading {
    font-size: 3rem;
    margin: 0;

    & p:first-child {
        font-weight: lighter;
    }
}

.bold-heading {
    font-weight: bolder;
}

@media screen and (min-width:300px) and (max-width:840px) {
    .section-4-container {
        width: 80%;
        margin-bottom: var(--margin-bottom-mobile);
    }

    .section-4-heading {
        font-size: 1.1rem;
    }

    .section-4-heading~p {
        font-size: 0.625rem;
    }
}