.section-5-container {
    background-color: #0B63E5;
    color: white;
    margin: auto;
    padding: 3rem;
    margin-bottom: 10rem;
}

.section-5-top {
    width: 80%;
    margin: auto;
    margin-bottom: 1rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.section-5-line1 {
    font-size: var(--font-size3);
    margin: 0;
    font-weight: lighter;
}

.section-5-line2 {
    font-size: var(--font-size3);
    margin: 0;
    font-weight: bold;
}

.section-5-line3 {
    font-size: var(--font-size8);
    margin: 0;
    width: 50%;
}

.section-5-bottom {
    width: 85%;
    display: flex;
    gap: 1rem;
    margin: auto;
    justify-content: space-between;

    & hr {
        rotate: 90%;
        border: 1.5px solid white;
    }
}

.section-5-bottom>div {
    width: 20%;
}

.section-5-head {
    display: flex;
    font-size: var(--font-size6);
    font-weight: bold;
    align-items: center;
    gap: 1.5rem;

    & img {
        width: 3rem;
        height: 3rem;
    }
}

.section-5-desc {
    width: 80%;
    font-size: var(--font-size4);
    font-weight: lighter;
}

@media screen and (min-width:300px) and (max-width:490px) {

    .section-5-container {
        padding: 2rem 0.5rem;
        margin-bottom: var(--margin-bottom-mobile);
    }

    .section-5-top {
        width: 92%;
    }

    .section-5-line1,
    .section-5-line2 {
        font-size: var(--font-size8);
    }

    .section-5-line3 {
        font-size: 0.75rem;
        width: 90%;
    }

    .section-5-bottom {
        flex-direction: column;
        width: 92%;
        gap: 0.5rem;

        & hr {
            width: 100%;
        }
    }

    .section-5-head {
        font-size: 1rem;

        & img {
            width: 2rem;
            height: 2rem;
        }
    }

    .section-5-desc {
        font-size: 0.75rem;
    }

    .section-5-bottom>div {
        width: 100%;
        display: flex;

        & img:nth-child(2) {
            width: 30%;
        }
    }

    .section-5-bottom>div:nth-child(3),
    .section-5-bottom>div:nth-child(7) {
        flex-direction: row-reverse;
        gap: 2rem;
    }
}

@media screen and (min-width:601px) and (max-width:840px) {
    .section-5-container {
        padding: 2rem 0.5rem;
        margin-bottom: 5rem;
    }

    .section-5-top {
        width: 80%;
    }

    .section-5-line1,
    .section-5-line2 {
        font-size: var(--font-size6);
    }

    .section-5-line3 {
        font-size: 1rem;
        width: 80%;
    }

    .section-5-bottom {
        flex-direction: column;
        width: 80%;
        gap: 0.5rem;

        & hr {
            width: 100%;
        }
    }

    .section-5-bottom-content {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-content: center;
    }

    .section-5-head {
        font-size: 1.5rem;

        & img {
            width: 2rem;
            height: 2rem;
        }
    }

    .section-5-desc {
        font-size: 1rem;
    }

    .section-5-bottom>div {
        width: 100%;
        display: flex;

        & img:nth-child(2) {
            width: 20%;
        }
    }

    .section-5-bottom>div:nth-child(3),
    .section-5-bottom>div:nth-child(7) {
        flex-direction: row-reverse;
        gap: 2rem;
    }
}