.data-engg-section-1 {
    display: flex;
    color: #20303F;
    width: 80%;
    margin: auto;
    margin-bottom: var(--margin-bottom-web);
    margin-top: 250px;
}

.data-engg-section-1-left {
    width: 75%;
}

.data-engg-section-1-left p:nth-child(1) {
    margin: 0;
    font-size: var(--font-size1);
    font-weight: bold;
}

.data-engg-section-1-left p:nth-child(2) {
    font-size: var(--font-size2);
    font-weight: lighter;
    margin: 0;
}

.data-engg-section-1-left span {
    margin: 0;
    font-size: var(--font-size2);
    font-weight: 900;
    margin: 0;
}

.data-engg-section-1-right {
    width: 60%;

    & img {
        width: 100%;
    }
}

.data-engg-section-3 {
    display: flex;
    width: 70%;
    justify-content: center;
    margin: auto;
    margin-bottom: 9rem;
}

.data-engg-section-3-left {
    width: 35%;
    display: flex;
    flex-direction: column;
    justify-content: center;

    & p:nth-child(1) {
        font-size: var(--font-size3);
        margin: 0;
    }

    & p:nth-child(2) {
        font-size: var(--font-size3);
        font-weight: bold;
        margin: 0;
        margin-bottom: 2rem;
    }

    & p:nth-child(3),
    & p:nth-child(4) {
        font-size: var(--font-size4);
        margin-bottom: 2rem;
    }
}

.data-engg-section-3-right {
    display: flex;
    justify-content: space-around;
    width: 50%;
    background-image: url('../../assets/Services/DataEngg/section-3.png');
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center 190px;
    border-radius: 1rem;
    margin: auto;
    padding: 2%;

    & p:nth-child(1) {
        font-size: var(--font-size6);
        font-weight: bold;
    }

    & p:nth-child(2) {
        font-size: 1.2rem
    }
}

.data-engg-section-3-right-1,
.data-engg-section-3-right-2 {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 1rem;
    padding: 0.5rem;


    & div {
        padding: 1rem;
        background: rgba(255, 255, 255, 0.7);
        backdrop-filter: blur(5px);
        border-radius: 1rem;
        border: 5px solid white;
        box-shadow: rgba(17, 17, 26, 0.1) 0px 0px 16px;
    }
}

.data-engg-section-4 {
    width: 70%;
    margin: auto;
    margin-bottom: 5rem;
}

.data-engg-section-4-container {
    padding: 5rem 0;
    background-image: url('../../assets/Services/DataEngg/ellipse.png');
    background-repeat: no-repeat;
    background-position: center;
}

.data-engg-section-4-top {
    font-size: 36px;
    margin-bottom: 2rem;

    p {
        margin: 0;
    }

    & p:nth-child(1) {
        font-weight: lighter;
    }

    & p:nth-child(2) {
        font-weight: bold;
    }
}

.data-engg-section-4-bottom-1,
.data-engg-section-4-bottom-2 {
    display: flex;
    justify-content: space-between;

    & div {
        padding: 1rem;
        border-radius: 1rem;
        background: white;
    }
}

.data-engg-section-4-bottom-1 div {
    width: 30%;
    margin-bottom: 2rem;
}

.data-engg-section-4-bottom-2 div {
    width: 45%;
}

.data-engg-section-4-bottom-1,
.data-engg-section-4-bottom-2 {
    & img {
        width: 70px;
        height: 70px;
    }
}

.section-4-bold-head {
    font-size: 32px;
    font-weight: bold;
}

.section-4-desc {
    font-size: 18px;
}

.data-engg-section-5 {
    padding: 1%;
    display: flex;
    font-size: 24px;
    font-weight: 400;
    justify-content: center;
    align-items: center;
    gap: 3rem;
    margin-bottom: 5rem;

    & div:nth-child(1) {
        width: max-content;
    }

    & div:nth-child(2) {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 2rem;
        width: 20%;
        height: 50px;

        & img {
            width: 50%;
        }
    }
}

.data-engg-section-6 {
    background-image: url('../../assets/Services/DataEngg/section-6.png');
    background-position: center;
    background-size: cover;
    margin-bottom: var(--margin-bottom-web);
    height: 600px;
    padding: 1%;

    & p:nth-child(1) {
        font-size: 72px;
        margin: 0;
    }

    & p:nth-child(2) {
        font-size: 24px;
    }
}

.section-6-content {
    display: flex;
    flex-direction: column;
    width: 45%;
    margin: auto;
    text-align: center;
    gap: 10rem;
    height: max-content;

    & div:nth-child(2) {
        display: flex;
        justify-content: center;
    }
}

.data-engg-section-7 {
    margin-bottom: 10rem;
}

.section-7-bg {
    background-image: url('../../assets/Services//DataEngg/wavelength.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.section-7-top {
    font-size: 28px;
    width: 70%;
    margin: auto;
    margin-bottom: 2rem;

    & p {
        margin: 0;
    }

    & p:nth-child(1) {
        font-weight: lighter;
    }
}

.section-7-bottom {
    width: 70%;
    margin: auto;
    display: flex;
    justify-content: space-between;

    & div {
        width: 30%;
        padding: 1%;
        border: 1px solid #90C8E5;
        background: white;
        border-radius: 1em;
    }

    & img {
        width: 100%;
        aspect-ratio: 16/9;
        border-radius: 1rem;
    }
}

.section-7-title,
.section-7-desc {
    font-size: 16px;
}

.section-7-title {
    font-weight: bold;
}

.section-7-head {
    font-size: 21px;
}

.data-engg-section-8 {
    margin-top: 0px;
    margin-bottom: -25%;
}

.data-engg-section-8-top {
    text-align: center;
    color: #20303F;
}

.data-engg-section-8-top>:nth-child(3) {
    margin-top: 12px;
    font-size: 20px;
    font-weight: 400;
    line-height: 30.66px;
}

.data-engg-section-8-top>:nth-child(1),
.data-engg-section-8-top>:nth-child(2) {
    font-size: 36px;
    line-height: 48px;
    margin: 0;
}

.data-engg-section-9 {
    margin-bottom: 10rem;
}

.section-9-top {
    width: 80%;
    margin: auto;
    margin-bottom: 2rem;
    font-size: 28px;
    color: #20303F;

    & p {
        margin: 0;
        font-weight: lighter;
    }
}

.section-9-bottom {
    display: flex;
    width: 80%;
    margin: auto;
    gap: 1rem;

    & ul {
        font-size: 14px;
    }
}

.data-engg-section-9-bottom-left,
.data-engg-section-9-bottom-right {
    width: 50%;
}

.data-engg-section-9-bottom-left {
    display: grid;
    grid-template-columns: repeat(2, 1fr);

    & div {
        padding: 1rem;
    }

    & div:nth-child(1) {
        border-bottom: 1px solid #4FC1AD;
        border-right: 1px solid #4FC1AD;
        border-radius: 40px;
    }

    & div:nth-child(2) {
        border-bottom: 1px solid #4FC1AD;
        border-left: 1px solid #4FC1AD;
        border-radius: 40px;
    }

    & div:nth-child(3) {
        border-top: 1px solid #4FC1AD;
        border-right: 1px solid #4FC1AD;
        border-radius: 40px;
    }

    & div:nth-child(4) {
        border-top: 1px solid #4FC1AD;
        border-left: 1px solid #4FC1AD;
        border-radius: 40px;
    }

    & img {
        width: 60px;
        height: 60px;
    }
}

.data-engg-section-9-bottom-right {
    background-image: url('../../assets/Services/DataEngg/section-9.png');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    font-size: 36px;
    display: flex;
    flex-direction: column;
    justify-content: end;
    align-items: center;
    color: white;
    border-radius: 1rem;

    & div {
        width: 90%;
        margin-bottom: 2rem;

        & p {
            margin: 0;
            font-weight: lighter;
        }
    }
}

.section-9-bottom-head {
    font-size: 20px;
    font-weight: bold;
}

/* Tablet Version */
@media screen and (min-width:960px) and (max-width: 1360px) {
    .data-engg-section-6 {
        height: 400px;
        padding: 1%;


        & p:nth-child(1) {
            font-size: 48px;
        }

        & p:nth-child(2) {
            font-size: 20px;
        }
    }

    .section-6-content {
        gap: 5rem
    }

    .data-engg-section-9 {
        margin-top: 25rem;
    }

    .section-9-bottom {
        flex-direction: column;
        gap: 2rem;
    }

    .data-engg-section-9-bottom-left,
    .data-engg-section-9-bottom-right {
        width: 100%;
    }

    .data-engg-section-9-bottom-right {
        height: 800px;
    }
}


/* Mobile Version */
@media screen and (min-width:300px) and (max-width: 500px) {
    .data-engg-section-1 {
        flex-direction: column;
        margin-bottom: var(--margin-bottom-mobile);
        width: 80%;
        margin-top: 0px;
    }

    .data-engg-section-1-left,
    .data-engg-section-1-right {
        width: 100%;
    }

    .data-engg-section-1-left p:nth-child(1) {
        font-size: 21px;
    }

    .data-engg-section-1-left p:nth-child(2),
    .data-engg-section-1-left span {
        font-size: 41px;
    }

    .data-engg-section-3 {
        flex-direction: column;
        width: 90%;
        margin-bottom: var(--margin-bottom-mobile);
    }

    .data-engg-section-3-left {
        width: 100%;

        & p:nth-child(1),
        & p:nth-child(2) {
            font-size: 28px;
        }

        & p:nth-child(2) {
            margin-bottom: 1rem;
        }

        & p:nth-child(3),
        & p:nth-child(4) {
            font-size: 14px;
            margin-bottom: 1rem;
        }
    }

    .data-engg-section-3-right {
        width: 100%;
        padding: 1%;
        gap: 0.5rem;
        background-position: center 140px;

        & p:nth-child(1) {
            font-size: 20px;
        }

        & p:nth-child(2) {
            font-size: 15px;
        }
    }

    .data-engg-section-3-right-1,
    .data-engg-section-3-right-2 {
        padding: 0;

        & div {
            padding: 0.5rem;
        }
    }

    .data-engg-section-4 {
        width: 100%;
        margin-bottom: 2.5rem;
    }

    .data-engg-section-4-container {
        width: 80%;
        padding: 3rem 0 0 0;
        margin: auto;
        background-image: none;
    }

    .data-engg-section-4-bottom-1,
    .data-engg-section-4-bottom-2 {
        flex-direction: column;
    }

    .data-engg-section-4-bottom-2 {
        gap: 2rem;
    }

    .data-engg-section-4-bottom-1 div {
        width: 90%;
        margin-bottom: 2rem;
        border: 1px solid #4fc2ac;
    }

    .data-engg-section-4-bottom-2 div {
        width: 90%;
        border: 1px solid #4fc2ac;
    }

    .section-4-bold-head {
        font-size: 24px;
    }

    .section-4-desc {
        font-size: 14px;
    }

    .data-engg-section-5 {
        flex-direction: column;
        gap: 0.5rem;
        margin-bottom: 2.5rem;

        & div:nth-child(2) {
            width: 50%;
        }
    }

    .data-engg-section-6 {
        height: 400px;
        padding: 1%;
        margin-bottom: var(--margin-bottom-mobile);

        & p:nth-child(1) {
            font-size: 36px;
        }

        & p:nth-child(2) {
            font-size: 16px;
        }
    }

    .section-6-content {
        gap: 5rem;
        width: 85%
    }

    .data-engg-section-7 {
        margin-bottom: 5rem;
    }

    .section-7-bg {
        background-image: none;
    }

    .section-7-bottom {
        flex-direction: column;
        gap: 1rem;
        width: 90%;

        & div {
            width: 90%;
            padding: 2%;
            margin: auto;
        }
    }

    .data-engg-section-8 {
        margin-bottom: -10%;
    }

    .data-engg-section-9 {
        margin-top: 10rem;
        margin-bottom: var(--margin-bottom-mobile);
    }

    .section-9-bottom {
        flex-direction: column;
    }

    .data-engg-section-9-bottom-left {
        width: 100%;
        grid-template-columns: repeat(1, 1fr);


        & img {
            width: 40px;
            height: 40px;
        }
    }

    .section-9-bottom-head {
        font-size: 24px;
    }

    .data-engg-section-9-bottom-right {
        width: 100%;
        font-size: 30px;
        height: 400px;
    }
}


/* Tablet Version */
@media screen and (min-width:600px) and (max-width: 1024px) {
    .data-engg-section-1 {
        margin-top: 0px;
        margin-bottom: var(--margin-bottom-mobile);
    }

    .data-engg-section-1-left p:nth-child(2),
    .data-engg-section-1-left span {
        font-size: 45px;
    }

    .data-engg-section-1-right {
        width: 70%;

        & img {
            width: 100%;
        }
    }

    .data-engg-section-3 {
        flex-direction: column;
        width: 90%;
        margin-bottom: var(--margin-bottom-mobile);
    }

    .data-engg-section-3-left {
        width: 100%;
    }

    .data-engg-section-3-right {
        width: 100%;
        padding: 1%;
        gap: 0.5rem;
        background-position: center 60px;
    }

    .data-engg-section-3-right-1,
    .data-engg-section-3-right-2 {
        padding: 0;

        & div {
            padding: 0.5rem;
        }
    }

    .data-engg-section-4 {
        width: 100%;
        margin-bottom: 5rem;
    }

    .data-engg-section-4-container {
        width: 80%;
        padding: 3rem 0 0 0;
        margin: auto;
        background-image: none;
    }

    .data-engg-section-4-bottom-1,
    .data-engg-section-4-bottom-2 {
        flex-direction: column;
    }

    .data-engg-section-4-bottom-2 {
        gap: 2rem;
    }

    .data-engg-section-4-bottom-1 div,
    .data-engg-section-4-bottom-2 div {
        width: 90%;
        margin: auto;
        border: 1px solid #4fc2ac;
    }

    .data-engg-section-4-bottom-1 div {
        margin-bottom: 2rem;
    }

    .data-engg-section-5 {
        margin-bottom: var(--margin-bottom-mobile);
    }

    .data-engg-section-6 {
        height: 400px;
        padding: 1%;

        & p:nth-child(1) {
            font-size: 36px;
        }

        & p:nth-child(2) {
            font-size: 16px;
        }
    }

    .section-6-content {
        gap: 6rem;
        width: 60%
    }

    .data-engg-section-7 {
        width: 100%;
        margin: auto;
        margin-bottom: 5rem;
    }

    .section-7-title,
    .section-7-desc {
        font-size: 14px;
    }

    .section-7-head {
        font-size: 18px;
    }

    .section-7-top {
        width: 90%;
    }

    .section-7-bottom {
        width: 90%;
        margin: auto;
    }

    .data-engg-section-9 {
        margin-top: 20rem;
        margin-bottom: var(--margin-bottom-mobile);
    }

    .section-9-bottom {
        flex-direction: column;
    }

    .data-engg-section-9-bottom-left {
        width: 100%;
        grid-template-columns: repeat(2, 1fr);

        & img {
            width: 40px;
            height: 40px;
        }
    }

    .data-engg-section-9-bottom-right {
        width: 100%;
        font-size: 30px;
        height: 800px;
    }
}

@media screen and (min-width:600px) and (max-width:700px) {
    .data-engg-section-3-right {
        background-position: center 150px;
    }
}