/* secton Two */
.sectionTwoTable{
    width: 80%;
    margin: auto;
    border-collapse: collapse;
    color :"white";
}

.TableHeadingTitle{
    font-size: 18px;
    line-height: 25px;
    border-radius: 10px 0px 0px 0px;
    border: 1px 0px 0px 0px;
    background: rgba(6, 41, 97);
    border: rgba(6, 41, 97);
    position: sticky;
    top:0;
}

.TableHeadingTitle th{
    font-weight: 500;
    line-height: 25px;
    padding: 15px 0px 15px 50px;
    text-align: left;
}
.TableHeadingTitle th:nth-child(1){
    width: 30%;
}
.TableHeadingTitle th:nth-child(2){
    width: 15%;
}

.TableHeadingTitle th:nth-child(3){
    width: 30%;
}

.TabledataHeading{
    background: rgba(21, 108, 254, 0.13);
    text-align: left;
    font-size: 16px;
    font-weight: 900;
    line-height: 22.4px;
    text-align: left;
}

.TabledataHeading th{
    padding: 10px 0px 5px 40px;
    border: rgba(3, 24, 57);
}

.Tabledata td{
    padding: 10px 0px 10px 40px;
    font-size: 14px;
    line-height: 19.6px;
}
tbody tr{
    border: 5px solid rgba(0, 11, 28, 1);
}

@media (max-width: 500px) {
    .sectionTwoTable{
        width: 100%;
    }
    .TableHeadingTitle{
        font-size: 10px;
    }
    .TableHeadingTitle th{
        font-weight: 500;
        line-height: 15px;
        padding: 5px 5px 5px 5px;
        text-align: left;
    }
    .TabledataHeading{
        text-align: left;
        font-size: 12px;
        font-weight: 900;
        line-height: 10px;
    }
    .TabledataHeading th{
        padding: 5px 5px 5px 5px;
    }
    
    .Tabledata td{
        padding: 5px 5px 5px 5px;
        font-size: 12px;
        line-height: 19.6px;
    }
    tbody tr{
        border: 2px solid rgba(0, 11, 28, 1);
    }
}

