.section-7 {
    width: 100%;
    margin-bottom: 10rem;
}

.section-7-desc>div {
    margin: 0;
    font-size: var(--font-size5);
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 0.2rem;

    & p {
        margin: 0;
    }
}

.section-7-bold {
    font-weight: bold;
}

@media screen and (min-width:300px) and (max-width:490px) {
    .section-7 {
        margin-bottom: var(--margin-bottom-mobile);
    }

    .section-7-desc>div {
        font-size: 0.85rem;
        gap: 0.1rem;

        & img {
            width: 9%;
        }
    }
}

@media screen and (min-width:601px) and (max-width:840px) {
    .section-7-desc>div {
        font-size: var(--font-size1);
        gap: 0.1rem;

        & img {
            width: 10%;
        }
    }
}