.CloudEngSectionSixContainer{
    display: flex;
    justify-content: space-between;
    gap: 60px;
    /* border: 1px solid; */
}

.CloudEngSectionSixContainer div{
    width: 50vh;
    flex: 1;
    padding: 2%;
    border: 1px solid;
    background-color: #0B63E5;
    color: white;
    border-radius: 5%;
}
.CloudEngSectionSixContainer h3{
    font-size: 28px;
    font-weight: 500;
    line-height: 40.04px;
    text-align: left;
}
.CloudEngSectionSixContainer li{
    font-size: 20px;
    font-weight: 400;
    line-height: 32px;
    text-align: left;
}

.CloudEngSectionSixContainer img{
    width: 50px;
}
hr{
    border: 1px solid;
}