#InteAuto-prt1,
#InteAuto-prt2,
#InteAuto-prt3,
#InteAuto-prt4,
#InteAuto-prt5,
#InteAuto-prt6 {
    margin: 0% 10% 100px 10%;
}

#InteAuto-prt1>p {
    font-size: 22px;
    margin-top: 250px;
    margin-bottom: -50px;
    font-weight: 600;
}

#InteAuto-prt1>div>h1 {
    font-size: 80px;
    font-weight: 100;
    width: 90%;
    /* margin-top: -100px; */
}

#InteAuto-prt1>div {
    align-items: center;
    display: flex;
}

#InteAuto-prt1>div>h1>span {
    font-weight: 800;
}

#InteAuto-prt1>div>img {
    width: 500px;
}

#InteAuto-prt2 {
    margin-top: -500px;
    display: flex;
    align-items: center;
}


.InteAuto-section-3-left>p>span:nth-child(1) {
    color: #0096FE;
}

.InteAuto-section-3-left>p>span:nth-child(2) {
    font-weight: 800;
}

.InteAuto-section-3-left {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;

    & p:nth-child(1) {
        font-size: 48px;
        margin: 0;
    }

    & p:nth-child(2) {
        font-size: 48px;
        font-weight: bold;
        margin: 0;
        margin-bottom: 2rem;
    }

    & p:nth-child(3),
    & p:nth-child(4) {
        font-size: var(--font-size4);
        margin-bottom: 2rem;
    }
}

.InteAuto-section-3-right {
    display: flex;
    justify-content: space-around;
    width: 50%;
    background-image: url('../../assets/Services/DataEngg/section-3.png');
    background-position: center;
    background-repeat: no-repeat;
    background-position: center 120px;
    margin: auto;
    padding: 2%;

    & p:nth-child(1) {

        font-size: var(--font-size6);
        font-weight: bold;
    }

    & p:nth-child(2) {
        font-size: 1.2rem
    }
}

.InteAuto-section-3-right-1,
.InteAuto-section-3-right-2 {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 60px;
    padding: 0.5rem;


    & div {
        border: 2px solid green;
        padding: 1rem;
        background: rgba(255, 255, 255, 0.7);
        backdrop-filter: blur(10px);
        border-radius: 1rem;
        border: 5px solid lightgray;
    }
}


#circle-inteAuto-compo {
    text-align: center;
}

#circle-inteAuto-compo>h1 {
    font-weight: 200;
    color: #292962;
}

#circle-inteAuto-compo>h1>span {
    font-weight: 800;
}

#InteAuto-prt3 {
    display: grid;
    gap: 10px;
    grid-template-columns: repeat(3, 1fr);
    background-image: url("../../assets/Services/CloudEngg/section3-bg.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: 900px;
    padding: 100px 0px;
}

.sec3-cont>img {
    width: 40px;
}

.sec3-cont {
    background-color: #ffffff;
    padding: 20px;
    border-radius: 20px;
    /* box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px; */
    /* background-position: top 500px; */
}

#InteAuto-prt3>:nth-child(2) {
    /* border: 1px solid black; */
    margin-left: 50px;
}

.sec3-cont>p {
    width: 70%;
}

#sec3-prt1>p {
    font-size: 20px;
}

#InteAuto-prt4 {
    margin-top: 0px;
}

#InteAuto-prt4>.sec4-prt1 {
    text-align: center;
}

.sec4-prt1>h1>span {
    font-weight: 1000;
}

.sec4-prt1>h1 {
    font-weight: 200;
}

.sec4-prt1>p {
    font-size: 20px;
}

.sec4-prt2 {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
    justify-content: space-around;
}

.sec4-prt2>div>img {
    width: 75px;
}

.sec4-prt2>div>p {
    width: 70%;
}

.sec4-prt2>div {
    background-color: #F3F7FF;
    padding: 20px;
    border-radius: 20px;
}


#InteAuto-prt5 {
    margin-top: 200px;
    margin-bottom: 100px;
}

#InteAuto-prt5>h1 {
    color: #292962;
    width: 40%;
    font-weight: 300;
}

#InteAuto-prt5>h1>span {
    font-weight: 600;
}

#InteAuto-sec5-cont {
    display: flex;
}

#inteauto-sec5-cont-prt1 {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    width: 70%;
    /* border: 1px solid green; */
}

#inteauto-sec5-cont-prt2 {
    background:
        linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 1)),
        url("../../assets/Services/InteAuto/inte-auto-sec5-bg.png");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}


#inteauto-sec5-cont-prt1>div>img {
    width: 50px;
}

#inteauto-sec5-cont-prt1>:nth-child(1) {
    padding: 15px;
    border-bottom: 1px solid #4FC1AD;
    border-right: 1px solid #4FC1AD;
    border-radius: 40px;
}

#inteauto-sec5-cont-prt1>:nth-child(2) {
    padding: 15px;
    border-bottom: 1px solid #4FC1AD;
    border-left: 1px solid #4FC1AD;
    border-radius: 40px;
}

#inteauto-sec5-cont-prt1>:nth-child(3) {
    padding: 15px;
    border-top: 1px solid #4FC1AD;
    border-right: 1px solid #4FC1AD;
    border-radius: 40px;
}

#inteauto-sec5-cont-prt1>:nth-child(4) {
    padding: 15px;
    border-top: 1px solid #4FC1AD;
    border-left: 1px solid #4FC1AD;
    border-radius: 40px;
}

#inteauto-sec5-cont-prt2 {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    width: 60%;
    padding-bottom: 50px;
    padding-left: 50px;
    border-radius: 30px;
}

#inteauto-sec5-cont-prt2>h1 {
    color: #ffffff;
    width: 80%;
    font-size: 55px;
    font-weight: 200;
}

#inteauto-sec5-cont-prt2>h1>span {
    font-weight: 600;
}

#InteAuto-prt6>h1 {
    font-weight: 200;
    font-size: 48px;
    align-items: center;
    text-align: center;
}

#InteAuto-prt6 {
    margin-top: 200px;
}

#InteAuto-prt6>h1>span>img {
    width: 62px;
    border-radius: 100%;
    vertical-align: middle;
    background-color: #0B63E5;
}

#InteAuto-prt6>h1>span {
    font-weight: 800;
}

@media (max-width: 1450px) {
    #InteAuto-prt1>div>img {
        width: 500px;
        height: 400px;
    }

    #InteAuto-prt4 {
        margin-top: 0px;
    }
}

@media (max-width: 1200px) {
    #InteAuto-prt1>div>img {
        width: 300px;
        height: 200px;
    }

    #InteAuto-prt1>div>h1 {
        font-size: 40px;
    }

    #InteAuto-prt2 {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        margin-top: 400px;
    }

    .InteAuto-section-3-left,
    .InteAuto-section-3-right {
        width: 90%;
    }

    .InteAuto-section-3-left,
    .InteAuto-section-3-left & p:nth-child(2) {

        & p:nth-child(1) {
            font-size: 38px;
            width: 100%s;
        }
    }

    #InteAuto-sec5-cont {
        display: flex;
        flex-direction: column-reverse;
    }

    #inteauto-sec5-cont-prt1 {
        width: 95%;
    }

    #inteauto-sec5-cont-prt2 {
        width: 90%;
    }

    #InteAuto-prt5>h1 {
        width: 80%;
    }
}

@media (max-width : 901px) {
    #InteAuto-prt3 {
        grid-template-columns: repeat(1, 1fr);
        background-image: none;
        background-size: 0px;
        padding: 0px;
        margin-left: 0px;
        
    }
    #InteAuto-prt1>p {
        font-size: 22px;
        margin-top: 0px;
        margin-bottom: 0px;
        font-weight: 600;
    }

    #sec3-prt1>h1,
    #sec3-prt1>p {
        margin-left: 10%;
    }

    .sec3-cont>p {
        width: 40%;
    }

    .sec4-prt2 {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (max-width: 600px) {
    #InteAuto-prt1>div {
        align-items: center;
        display: flex;
        flex-direction: column;
    }
    #InteAuto-prt1>p {
        font-size: 22px;
        margin-top: 0px;
        margin-bottom: 0px;
        font-weight: 600;
    }

    #InteAuto-prt2 {
        margin-top: 00px;
    }

    .InteAuto-section-3-left,
    .InteAuto-section-3-left & p:nth-child(2) {

        & p:nth-child(1) {
            font-size: 28px;
            width: 100%;
        }

        & p:nth-child(3) {
            font-size: 17px;
        }
    }

    .InteAuto-section-3-right {
        margin-left: -10px;
        width: 110%;

        & p:nth-child(1) {
            font-size: 22px;
            font-weight: bold;
        }

        & p:nth-child(2) {
            font-size: 14px
        }
    }

    .InteAuto-section-3-right-1,
    .InteAuto-section-3-right-2 {
        & div {
            border: 2px solid green;
            padding: 1rem;
            background: rgba(255, 255, 255, 0.7);
            backdrop-filter: blur(10px);
            border-radius: 1rem;
            border: 5px solid lightgray;
        }
    }



    #InteAuto-prt3 {
        margin-left: -50px;
        margin-right: 20%;
        margin-top: -50px;
    }

    #sec3-prt1>h1,
    #sec3-prt1>p {
        margin-left: 20%;
    }

    .sec4-prt2 {
        grid-template-columns: repeat(1, 1fr);
    }

    #InteAuto-prt5 {
        margin-top: -50px;
    }

    #InteAuto-prt5>h1 {
        font-size: 25px;
        width: 100%;
        text-align: center;
    }

    #inteauto-sec5-cont-prt1 {
        width: 95%;
        grid-template-columns: repeat(1, 1fr);
    }

    #inteauto-sec5-cont-prt1 {
        width: 100%;
    }

    #inteauto-sec5-cont-prt2>h1 {
        color: #ffffff;
        width: 90%;
        font-size: 28px;
        font-weight: 200;
    }

    #inteauto-sec5-cont-prt2 {
        width: 85%;
    }

    #InteAuto-prt6 {
        margin-top: -20px;
        margin-bottom: 0px;
    }

    #InteAuto-prt6>h1 {
        font-weight: 200;
        font-size: 23px;
        align-items: center;
    }

    #InteAuto-prt6>h1>span>img {
        width: 42px;
    }
}